body {
    background: url("../images/background.png"); //no-repeat center fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Nerko One', cursive;
    font-weight: bolder;
    color: rgb(255, 255, 255);
    background-size: cover;
    height: 100vh;
}
header {
    display: flex;
    align-items: center;
    justify-content: space-around;

}


.title {
    font-size: 60px;
    color: white;
    text-shadow: rgb(113, 118, 38);
    padding-left: 87px;
}
.score, .movement {
    font-size: 45px;
    color: rgb(255, 255, 255);
    text-shadow: 4px 4px 8px rgb(48, 101, 65);
    // text-transform: uppercase;
    font-weight: 200;
}

.level {
    text-align: left;
    color: rgb(140, 142, 146);
}

.goal {
    text-align: left;
    color: rgb(140, 142, 146);
}

#board {
    width: 500px;
    height: 500px;
    background-color: rgb(42, 100, 48);
    border: 5px solid rgb(103, 26, 26);
    border-radius: 10px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

#board img {
    width: 55px;
    height: 55px;
}


