.startGameModal {
    display: block; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(255, 254, 254); /* Fallback color */
    background-color: rgba(255, 255, 255, 0.4); /* Black w/ opacity */
}


.startGameModalContent { 
    margin: 2% auto; /* 15% from the top and centered */
    padding: 20px;
    width: 65%; /* Could be more or less, depending on screen size */
    height: 85%;
   
}
.video {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.game-start-model-label, .gamePlay, .gameInstructions {
    top: -600px;
    position: relative;
    text-align: center;
    margin: 15px;
    color: rgb(14, 46, 10);
    font-family: 'Nerko One', cursive;
    text-shadow: 4px 4px 8px rgb(255, 255, 255);
    font-size: 100px;
}

.gamePlay, .gameInstructions {
    color: rgb(6, 33, 13);
    float: right;
    font-size: 28px;
    font-weight: bold;
    border: 5px solid rgb(103, 26, 26);
    border-radius: 10px;
}
  
.gamePlay:hover,
.gameInstructions:hover,

.gamePlay:focus, .game-won-label:focus {
    color: rgb(241, 30, 30);
    text-decoration: none;
    cursor: pointer;
}

.gamePlay, .gameInstructions {
    display: block;
    position: relative;
    padding: 12px 27px;
    border-radius: 12px;
    font-size: 1.1rem;
    color: rgb(65, 11, 11);
    background: rgb(255, 255, 255);
    will-change: transform;
    transform: translateY(-4px);
    transition: transform 600ms cubic-bezier(.3, .7, .4, 1);
}


