.nextLevel:hover,
.restart:hover,

.nextLevel:focus, .restart:focus {
    color: rgb(59, 11, 11);
    text-decoration: none;
    cursor: pointer;
}

.nextLevel, .restart {
    background: linear-gradient(to bottom right, #5b4604, #b5af3b);
    border: 0;
    border-radius: 12px;
    color: #FFFFFF;
    cursor: pointer;
    display: inline-block;
    font-family: -apple-system,system-ui,"Segoe UI",Roboto,Helvetica,Arial,sans-serif;
    font-size: 16px;
    font-weight: 500;
    line-height: 2.5;
    outline: transparent;
    padding: 0 1rem;
    text-align: center;
    text-decoration: none;
    transition: box-shadow .2s ease-in-out;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
}


